body {
  margin: 0;
  font-family: 'Fira Sans', 'monospace', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Fira Sans', 'monospace', source-code-pro, Menlo, Monaco, Consolas, 'Courier New';
}

.header {
    display: flex;
    justify-content: space-between;
    flex: 1 1;
    padding: 12px;
    padding-top: 20px;
    padding-bottom: 20px;
    position: relative;
    align-items: center;
    background-color: rgba(85,26,139,255)
}

.menu {
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
    align-items: center;
}

.menu-icon {
    font-size: 26px;
}

.mobile-menu {
    display: none;
}

.web-menu {
    display: flex;
}

.name {
    display: flex;
    font-size: 20px;
    color: white;
}

a.name {
    text-decoration: none;
    color: white;
}

@media only screen and (max-width: 720px) {
.mobile-menu{
    display: flex;
}
.web-menu{
    display: none;
}
.name {
    font-size: 18px;
    color: white;
}
}


.web {
    display: flex;
    flex: 1 1;
    align-items: center;
}

.web-option {
    font-size: 20px;
    margin-right: 32px;
    font-weight: 600;
}

.web-option a {
    text-decoration: none;
    color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
}
.mobile {
    height: 100vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    padding: 30px 12px;
    top: -6px;
    display: flex;
    flex-direction: column;
    background-color: white;
}

.close-icon {
    font-size: 20px;
    display: flex;
    justify-content: flex-end;
}

.mobile-options {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 0px;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.mobile-option {
    font-size: 20px;
    margin: 36px 8px;
    display: flex;
}

.mobile-option a {
    text-decoration: none;
    color: black;
    display: flex;
}
.body {
    padding: 12px;
    padding-bottom: 36px
}
.social-contact {
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
}

.social-icon {
    color: rgba(85,26,139,255)
}

a.social-icon {
    margin: 0 1rem;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    display: inline-block;
  }

a.social-icon:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.about {
  padding-bottom: 48px;
}

.about-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.picture {
  height: 400px;
}

.about-info {
  font-size: 22px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  grid-gap: 14px;
  gap: 14px;
}

.education {
  font-style: italic;
  color: rgb(169, 91, 169);
}

.info-name {
  font-size: 54px;
  font-weight: 600;
  font-family: 'Fira Mono', 'Fira Sans', monospace;
  color: rgba(85,26,139,255)
}

.hello {
  font-family: 'Fira Mono', 'Fira Sans', monospace;
  font-weight: 500;
}

.statement {
  padding-top: 24px;
  font-family: 'Fira Mono', 'Fira Sans', monospace;
  font-weight: 500;
}

@media only screen and (max-width: 1096px) {
  .info-name {
    font-size: 38px;
  }
}


@media only screen and (max-width: 720px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .info-name {
    font-size: 34px;
  }
  .statement {
    font-size: 18px;
  }
  .hello {
    font-size: 18px;
  }
  .education {
    font-size: 18px;
  }
}

@media only screen and (max-width: 430px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 350px;
  }
  
}

@media only screen and (max-width: 430px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 350px;
  }
  .info-name {
    font-size: 34px;
    font-weight: 600;
    font-family: 'Fira Mono', 'Fira Sans', monospace;
    color: rgba(85,26,139,255)
  }
}

@media only screen and (max-width: 360px) {
    .about-top {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .picture {
      height: 250px;
    }
  }




 

html {
    scroll-behavior: smooth;
}
.home {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
}

@media only screen and (max-width: 1024px){
.home{
    width: 90%;
}
}

@media only screen and (max-width: 512px) {
.home{
    width: 100%;
}
}



